// import { GLShaderCommon } from '../Utility/GLShaderCommon';
// import { AttribLocations, UniformLocations } from './AttribLocations';
import { Model } from './Model';

export enum ATTR {
    ATTR_POSITION_NAME = 'a_position',
    ATTR_POSITION_LOC = 0,

    ATTR_NORMAL_NAME = 'a_norm',
    ATTR_NORMAL_LOC = 1,

    ATTR_UV_NAME = 'a_uv',
    ATTR_UV_LOC = 2,

    ATTR_COLOR_NAME = 'a_color',
    ATTR_COLOR_LOC = 4,
}

export class AttribLocations {

    position: number;
    norm: number;
    uv: number;

    constructor(gl: WebGL2RenderingContext, program: WebGLProgram) {
        this.position = gl.getAttribLocation(program, ATTR.ATTR_POSITION_NAME);
        this.norm = gl.getAttribLocation(program, ATTR.ATTR_NORMAL_NAME);
        this.uv = gl.getAttribLocation(program, ATTR.ATTR_UV_NAME);
    }
}
export class UniformLocations {

    perspective: WebGLUniformLocation | null;
    modelMatrix: WebGLUniformLocation | null;
    cameraMatrix: WebGLUniformLocation | null;
    mainTexture: WebGLUniformLocation | null;
    
    constructor(gl: WebGL2RenderingContext, program: WebGLProgram) {
        this.perspective =	gl.getUniformLocation(program, 'uPMatrix');
        this.modelMatrix =	gl.getUniformLocation(program, 'uMVMatrix');
        this.cameraMatrix =	gl.getUniformLocation(program, 'uCameraMatrix');
        this.mainTexture =	gl.getUniformLocation(program, 'uMainTex' );
    }
}

export class Shader {

    gl: WebGL2RenderingContext;
    program: WebGLProgram | null;

    attribLoc: AttribLocations;
    uniformLoc: UniformLocations;

    vShader: WebGLShader | null;
    fShader: WebGLShader | null;

    constructor(gl: WebGL2RenderingContext , vertShaderSrc: string, fragShaderSrc: string, needValidate: boolean = true ) {
        this.gl = gl;

        this.vShader = ShaderUtility.CreateShader(gl, gl.VERTEX_SHADER as unknown as WebGLShader, vertShaderSrc);
        this.fShader = ShaderUtility.CreateShader(gl, gl.FRAGMENT_SHADER as unknown as WebGLShader, fragShaderSrc);
        
        if (this.vShader && this.fShader ) { 
            let program = ShaderUtility.CreateProgram(gl, this.vShader, this.fShader, needValidate);

            if (program) {
                this.program = program;
                gl.useProgram(this.program);
                this.attribLoc = new AttribLocations(gl, this.program);
                this.uniformLoc = new UniformLocations(gl, this.program);
                return;
                // gl.useProgram(this.program);
                // this.attribLoc = new AttribLocations(gl, this.program);
                // this.uniformLoc = new UniformLocations(gl, this.program);
                // gl.useProgram(null);
                // this.uniformLoc = {};	// TODO : Replace in later lessons with get standardUniformLocations.
            }

            // Note :: Extended shaders should deactivate shader when done calling super and setting up custom parts in the constructor.
        }
    }
    // ...................................................
    // Methods
    activate() {
         this.gl.useProgram(this.program); return this; 
    }
    deactivate() { 
        this.gl.useProgram(null); return this; 
    }

    // function helps clean up resources when shader is no longer needed.
    dispose() {
        // unbind the program if its currently active
        if (this.gl.getParameter(this.gl.CURRENT_PROGRAM) === this.program) {this.gl.useProgram(null); }
        this.gl.deleteProgram(this.program);
    }
    setPerspective(matData: Float32Array) {
        this.gl.uniformMatrix4fv(this.uniformLoc.perspective, false, matData); 
        return this; 
    }
    setModelMatrix(matData: Float32Array) {
        this.gl.uniformMatrix4fv(this.uniformLoc.modelMatrix, false, matData); 
        return this; 
    }
    setCameraMatrix(matData: Float32Array) {
        this.gl.uniformMatrix4fv(this.uniformLoc.cameraMatrix, false, matData); 
        return this; 
    }
    // ...................................................
    // RENDER RELATED METHODS

    // Setup custom properties
    preRender() {
        //
    } 
    // Handle rendering a model
    renderModel(model: Model) {
        this.setModelMatrix(model.transform.getViewMatrix());	// Set the transform, so the shader knows where the model exists in 3d space
        this.gl.bindVertexArray(model.vao.vao);	// Enable VAO, this will set all the predefined attributes for the shader
        
        if (model.vao.noCulling) {this.gl.disable(this.gl.CULL_FACE); }
        if (model.vao.doBlending) {this.gl.enable(this.gl.BLEND); }
        
        if (model.vao.indexCount) {
            this.gl.drawElements(model.vao.drawMode, model.vao.indexCount, this.gl.UNSIGNED_SHORT, 0); 
        } else {
            this.gl.drawArrays(model.vao.drawMode, 0, model.vao.vertexCount);
        }

        this.gl.bindVertexArray(null);
        if (model.vao.noCulling) {this.gl.enable(this.gl.CULL_FACE); }
        if (model.vao.doBlending) {this.gl.disable(this.gl.BLEND); }
        
        return this;
    }
}

class ShaderUtility {
    static CreateProgram(gl: WebGL2RenderingContext, vertexShader: WebGLShader, fragmentShader: WebGLShader, needValidate: boolean = true): WebGLProgram | null {
        const program = gl.createProgram();
        if (program === null ) {return null; }
        gl.attachShader(program, vertexShader);
        gl.attachShader(program, fragmentShader);

        gl.bindAttribLocation(program, ATTR.ATTR_POSITION_LOC, ATTR.ATTR_POSITION_NAME);
        gl.bindAttribLocation(program, ATTR.ATTR_NORMAL_LOC, ATTR.ATTR_NORMAL_NAME);
        gl.bindAttribLocation(program, ATTR.ATTR_UV_LOC, ATTR.ATTR_UV_NAME);
        
        gl.linkProgram(program);

        // Check that shader program was able to link to WebGL
        if (!gl.getProgramParameter(program, gl.LINK_STATUS)) {
            const error = gl.getProgramInfoLog(program);
            console.log('Failed to link program: ' + error);
            gl.deleteProgram(program);
            gl.deleteShader(fragmentShader);
            gl.deleteShader(vertexShader);
            return null;
        }
        if (needValidate) {
            gl.validateProgram(program);
            if (!gl.getProgramParameter(program, gl.VALIDATE_STATUS)) {
                console.error('Failed to validate program', gl.getProgramInfoLog(program));
                gl.deleteProgram(program);
                gl.deleteShader(fragmentShader);
                gl.deleteShader(vertexShader);
                return null;
            }
        }
        gl.deleteShader(fragmentShader);
        gl.deleteShader(vertexShader);
        return program;
    }
    static CreateShader(gl: WebGL2RenderingContext, type: WebGLShader, shaderString: string) {
        // console.log(type);
        const shader = gl.createShader(type as unknown as number);
        if (shader === null) {return null; }
        gl.shaderSource(shader, shaderString);
        gl.compileShader(shader);
        const suc = gl.getShaderParameter(shader, gl.COMPILE_STATUS);
        if (suc) { return shader; }
        console.log(gl.getShaderInfoLog(shader));
        gl.deleteShader(shader);
        return null;
    }
}